.zones-stats {
  &__zones {
    background-color: var(--playground-color);
    user-select: none;
  }

  .zone {
    &_low {
      fill: rgba(255, 0, 0, 0.4);
    }

    &_medium {
      fill: rgba(255, 251, 0, 0.4);
    }

    &_high {
      fill: rgba(44, 226, 27, 0.4);
    }
  }
}
